.tasks {
    list-style: none;
    margin: 0;
    padding: 0;
}

.tasks__item {
    padding: 10px;
    border-bottom: 1px solid #ddd;
    display: grid;
    grid-template-columns: auto 1fr auto;
    align-items: center;
    grid-gap: 10px;
}


.tasks__item--done {
    word-break: break-word;
    text-decoration: line-through;
}

.tasks__item--hidden {
    display: none;
}

.tasks__content--done {
    text-decoration: line-through;
}

.tasks__button {
    width: 30px;
    height: 30px;
    color:white;
    border: none;
    padding: 0;
    transition:  background 0.5s;
    background-color: green;
}

.tasks__button:hover {
    background-color: rgb(0, 157, 0);
}

.tasks__button:active {
    background-color: rgb(0, 186, 0);
}

.tasks__button--remove {
    background-color: rgb(212, 19, 19);
}

.tasks__button--remove:hover {
    background-color: rgb(230, 11, 11);
}

.tasks__button--remove:active {
    background-color: rgb(255, 0, 0);
}
